'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-read-only-property';


// MAIN //

/**
* Top-level namespace.
*
* @namespace ns
*/
var ns = {};

/**
* @name tempHttpServer
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/net/disposable-http-server}
*/
import tempHttpServer from './../disposable-http-server';
setReadOnly( ns, 'tempHttpServer', tempHttpServer );

/**
* @name httpServer
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/net/http-server}
*/
import httpServer from './../http-server';
setReadOnly( ns, 'httpServer', httpServer );

/**
* @name http2SecureServer
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/net/http2-secure-server}
*/
import http2SecureServer from './../http2-secure-server';
setReadOnly( ns, 'http2SecureServer', http2SecureServer );

/**
* @name simpleHttpServer
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/net/simple-http-server}
*/
import simpleHttpServer from './../simple-http-server';
setReadOnly( ns, 'simpleHttpServer', simpleHttpServer );


// EXPORTS //

export default ns;
